import './scss/main.scss'

const projects = require('./projects.json')

const i = require('./img/609732.svg')

const P = document.getElementById('projects')

for (const project of projects) {
    console.log(project.icon, typeof project.icon)
    if (project.icon) {
        var icon = require(project.icon)
    } else {
        var icon = require('./img/609732.svg')
    }

    P.innerHTML = P.innerHTML + `
    <div class="row project">
        <div class="work__item">
            <h3 class="heading-tertiary">${project.name}</h3>
            <img src="${icon}" alt="${project.name}" class="work__img" />
            <p class="work__description">
                ${project.description}
            </p>
        </div>
    </div>
    `
}


